@import "reset.scss";
@import "fonts.scss";
@import "grid.scss";
@import "vars.scss";

html, body {
	overflow-x: hidden;
}
body {
	position: relative;
	background: #F2F2F2;
	font-family: Muller;
	font-size: 18px;
	line-height: 100%;
	font-weight: 400;
	line-height: 1;
	@media screen and (max-width : 1024px) {
		font-size: 17px;
	}
	@media screen and (max-width : 767px) {
		font-size: 16px;
	}
}
img {
	max-width: 100%;
}

h1.title,
h2.title {
	font-size: 56px;
	@media screen and (max-width : 1024px) {
		font-size: 44px;
	}
	@media screen and (max-width : 767px) {
		font-size: 36px;
	}
	@media screen and (max-width : 639px) {
		font-size: 30px;
	}
}

h1.title {
	margin: 0;
	line-height: 142.8%;
	@media screen and (max-width : 1024px) {
		line-height: 120%;
	}
}

h2.title {
	line-height: 100%;
}

h3.title {
	font-size: 42px;
	line-height: 133%;
	@media screen and (max-width : 1024px) {
		font-size: 34px;
	}
	@media screen and (max-width : 767px) {
		font-size: 28px;
	}
	@media screen and (max-width : 639px) {
		font-size: 26px;
	}
}

h4.title {
	font-size: 32px;
	line-height: 156%;
	font-weight: 500;
	@media screen and (max-width : 1024px) {
		font-size: 24px;
	}
	@media screen and (max-width : 767px) {
		font-size: 20px;
	}
}

h5.title {
	font-size: 24px;
	@media screen and (max-width : 1024px) {
		font-size: 22px;
	}
	@media screen and (max-width : 767px) {
		font-size: 18px;
	}
}

a:hover {
	color: $accent;
	transition: $transition;
}

.light {
	color: $light;
}

.align-center {
	text-align: center;
}

.title.light {
	color: $light;
}

.accent {
	color: $accent;
}

.title.accent {
	color: $accent;
}

.title {
	margin: 0;
	font-weight: 900;
	color: $dark;
}

.header {
	font-size: 13px;
	position: relative;
	z-index: 10;
}

.header__top {
	z-index: 1;
	position: relative;
	background: $light;
}

.header-top__inner {
	display: flex;
	justify-content: space-between;
	padding: 10px 0;
}

.header__top-left {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.header__top-middle {
	@media screen and (min-width : 992px) {
	 display: none;
	}
}
.header__logo {
	max-width: 197px;
}

.header__slogan {
	display: inline-block;
	margin-left: 25px;
	max-width: 135px;
	line-height: 20px;
	@media screen and (max-width : 767px) {
	 display: none;
	}
}

.header__top-right {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: flex-end;
	@media screen and (max-width : 599px) {
	 display: none;
	}

	.contact-info__phone {
		font-size: 22px;
		font-weight: 900;
	}
}

.menu .header__top-right {
	display: flex;
}
#menu.mm-spn ul {
	position: static;
}
.mmenu-info {
	@media screen and (min-width : 992px) {
	 display: none;
	}
}
.mm-spn .contact-info__email a{
	@media screen and (max-width : 991px) {
	 display: inline;
	}
}
.mm-spn .contact-info__phone a{
	@media screen and (max-width : 991px) {
	 padding-left: 0;
	}
}
.header__bottom {
	margin-bottom: -40px;
	height: 40px;
}

.header-bottom__inner {
	padding-top: 22px;
}

.main-nav {
	.menu {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		@media screen and (max-width : 991px) {
			display: flex;
			flex-direction: column;
		}

		.menu-item {
			padding-right: 18px;
			text-transform: uppercase;
			position: relative;
		}

		a {
			color: $dark;
			font-weight: 900;
			&:hover {
				font-weight: 900;
			}
			&:hover::after {
				z-index: -1;
				position: absolute;
				bottom: 0;
				left: 2px;
				content: '';
				transform: skew(-45deg, 0deg);
				background: $accent;
				height: 4px;
				width: calc(100% - 20px);
			}
		}
	}
}

.hero {
	padding: 100px 0 62px;
	position: relative;
	.sec-hero__bg {
		z-index: -1;
		position: absolute;
		top: 0;
		right: 0;
		filter: blur(10px);
		background: url(../img/hero-bg.png);
		background-position: right center;
		background-size: contain;
		background-repeat: no-repeat;
		height: 100%;
		width: 100%;
	}
	.map-group {
		margin-bottom: 40px;
		@media screen and (max-width : 1024px) {
		 margin-bottom: 20px;
		}
	}
	.line-dash-1  {
		left: 0;
		top: 14%;
	}
	.line-dash-2  {
		left: 66.6666%;
		top: 21%;
	}
	.line-dash-3  {
		left: 33.3333%;
		top: 50%;
	}
	.line-dash-4  {
		left: 66.6666%;
		top: 95%;
	}
}



.hero__inner {
	position: relative;
	z-index: 1;

}

.hero__body {
	display: flex;
	justify-content: space-between;
	@media screen and (max-width : 1199px) {
	 flex-direction: column;
	}

	p {
		margin-top: 26px;
		margin-bottom: 40px;
		line-height: 30px;
		font-weight: 300;
		@media screen and (max-width : 1024px) {
			margin-top: 10px;
			margin-bottom: 15px;
		}
	}
}

.hero__content {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: flex-start;
}

.map-group {
	display: flex;
	align-items: center;

	.map-icon {
		height: 92px;
		width: 115px;
	}

	.map-group__link {
		position: relative;
		display: inline-block;
		margin-top: 7px;
		margin-left: 30px;
		@media screen and (max-width : 479px) {
		 margin-left: 10px;
		}

		&::after {
			position: absolute;
			bottom: 0;
			left: 0;
			content: '';
			background-color: $dark;
			height: 1px;
			width: 100%;
		}
	}
}

.hero__cta {
	z-index: 1;
	margin-top: auto;
	padding: 15px 18px;
	background-color: $accent;
	max-width: 399px;
	font-style: italic;
	color: $light;

	.cta-head {
		font-weight: 900;
	}
}

.hero__media {
	position: absolute;
	bottom:30px;
	right: 0px;
	@media screen and (max-width : 1600px) {
	 transform: translateX(25%) scale(0.75);
	}
	@media screen and (max-width : 991px) {
	 transform: translateX(50%) scale(0.5);
	}
	@media screen and (max-width : 639px) {
	 display: none;
	}

	.light {
		position: absolute;
		bottom: 60px;
		left: 404px;
	}
}

.sell {
	position: relative;
	padding: 70px 0 185px;
	background: url(../img/sell_bg.png);
	background-position: center bottom;
	background-size: cover;
	background-repeat: no-repeat;

	.sell__bg {
		position: absolute;
		bottom: -90px;
		left: 0px;
		@media screen and (max-width : 1600px) {
			transform: translateX(-25%) scale(0.75);
		 }
		 @media screen and (max-width : 991px) {
			transform: translateX(-50%) scale(0.5);
		 }
		//  @media screen and (max-width : 639px) {
		// 	display: none;
		//  }
	}

	.sell__light1 {
		position: absolute;
		top: -160px;
		right: 0;
	}

	.sell__light2 {
		position: absolute;
		bottom: -50px;
		left: 0px;
		@media screen and (max-width : 1600px) {
			left: -240px;
		}
	}
	.line-dash-1  {
		left: 0;
		top: 49%;
	}
	.line-dash-2  {
		left: 33.3333%;
		top: 20.5%;
	}
	.line-dash-3  {
		right: -1px;
		top: 80%;
	}
}
.sell__body {
	position: relative;
	z-index: 1;
}

.sell-options {
	margin-bottom: 50px;

	[class^=col-] {
		display: flex;
		margin-bottom: 30px;
	}

	h3.title {
		margin-bottom: 50px;
	}
}

.sell-options__item {
	margin-bottom: 30px;
	padding: 25px 10px 30px;
	background: $light;
	text-align: center;

	&-title {
		margin-bottom: 20px;
	}

	&-content {
		line-height: 24px;
	}
}

.advantages {
	[class^=col-] {
		display: flex;
		margin-bottom: 30px;
	}

	h3.title {
		margin-bottom: 50px;
	}
}

.advantages__item {
	position: relative;
	margin-bottom: 30px;
	border: 1px solid $accent;
	border-bottom-width: 10px;
	padding-bottom: 30px;
}
.advantages--style1 {
	.advantages__item-title {
		position: absolute;
		top: 1px;
		left: 8px;
		font-size: 250px;
		line-height: 250px;
		font-weight: 900;
		-webkit-text-fill-color: transparent;
		-webkit-text-stroke-width: 1px;
	}
	.advantages__item-content {
		z-index: 1;
		position: relative;
		padding-top: 100px;
		padding-left: 38px;
		line-height: 28px;

		span {
			font-weight: 900;
		}
	}
}
.advantages--style2 {
	.advantages__item-title {
		position: relative;
		margin-bottom: 40px;
		padding-top: 160px;
		padding-left: 25px;
		font-size: 18px;
		font-weight: 900;
		@media screen and (max-width : 767px) {
		 margin-bottom: 10px;
		}
		&::before {
			position: absolute;
			top: -38px;
			left: 0;
			content: '';
			background-position: center;
			background-repeat: no-repeat;
			height: 100%;
			width: 100%;
		}
	}
	.advantages__item-content {
		padding-left: 25px;
		font-size: 18px;
		line-height: 155.5%;
	}
	.advantages__item-1 .advantages__item-title{
		&::before {
			background-image: url(../img/advantages-01.png);
		}
	}
	.advantages__item-2 .advantages__item-title{
		&::before {
			background-image: url(../img/advantages-02.png);
		}
	}
	.advantages__item-3 .advantages__item-title{
		&::before {
			background-image: url(../img/advantages-03.png);
		}
	}
	.advantages__item-4 .advantages__item-title{
		&::before {
			background-image: url(../img/advantages-04.png);
		}
	}
	.advantages__item-5 .advantages__item-title{
		&::before {
			background-image: url(../img/advantages-05.png);
		}
	}
	.advantages__item-6 .advantages__item-title{
		&::before {
			background-image: url(../img/advantages-06.png);
		}
	}
}


.sell-alone {
	padding-top: 40px;
	position: relative;

	[class^=col-] {
		display: flex;
	}

	.large-item {
		flex-basis: 100%;
		margin: 0 auto;
		max-width: 630px;
	}
	.line-dash-1  {
		left: 0;
		top: 61%;
	}
	.line-dash-2  {
		left: 66.6666%;
		top: 22%;
	}
	.line-dash-3  {
		right: -1px;
		top: 76%;
	}
}
.sell-alone__inner {
	position: relative;
	z-index: 1;
}

.sell-alone__header {
	margin-bottom: 80px;

	.title {
		margin-bottom: 5px;
	}

	.descr {
		font-size: 16px;
	}
}

.sell-alone__item {
	position: relative;
	margin-bottom: 55px;
	padding: 65px 20px 45px;
	background: $light;
	font-size: 16px;
	line-height: 162%;
	box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.1);

	&-icon {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translate(-50%, -50%);
		display: inline-flex;
		border: 1px solid $accent;
		padding: 19px 15px;
		background-color: $light;
	}
}

.buyout {
	position: relative;
	padding: 50px 0;
	.line-dash-1  {
		left: 33.3333%;
		top: 57%;
	}
}
.buyout__inner {
	position: relative;
	z-index: 1;
}

.buyout__header {
	margin-bottom: 30px;
}

.buyout__body {
	border: 10px solid $accent;
	padding: 50px 58px;
	@media screen and (max-width : 767px) {
		padding: 30px 15px;
	}
}

.buyout__list-title {
	margin-bottom: 40px;
}

.buyout__list-item {
	position: relative;
	margin-bottom: 20px;
	padding-left: 23px;
	font-size: 15px;
	line-height: 160%;

	&::before {
		position: absolute;
		top: 7px;
		left: 0;
		content: '';
		border-radius: 50%;
		background: $accent;
		height: 8px;
		width: 8px;
	}
}

.buyout__decor {
	position: absolute;
	top: -140px;
	right: 0;
	z-index: 1;
	@media screen and (max-width : 1700px) {
		transform: translateX(25%) scale(0.75);
	 }
	 @media screen and (max-width : 1199px) {
		transform: translateX(50%) scale(0.5);
	 }
	 @media screen and (max-width : 639px) {
		display: none;
	 }
}
.comission {
	position: relative;
	padding-top: 40px;
	padding-bottom: 165px;
	.sec-comission__bg {
		z-index: -1;
		position: absolute;
		top: 0;
		right: 0;
		filter: blur(10px);
		background: url(../img/comission-bg.png);
		background-position: right center;
		background-size: cover;
		background-repeat: no-repeat;
		height: 100%;
		width: 100%;
	}
	.line-dash-1  {
		left: 0;
		top: 1%;
	}
	.line-dash-2  {
		left: 33.3333%;
		top: 61%;
	}
	.line-dash-3  {
		right: -1px;
		top: 27%;
	}
}
.comission__inner	 {
	position: relative;
	z-index: 1;
}
.comission__header {
	margin-bottom: 20px;
}

.comission__list {
	padding-right: 70px;
	padding-left: 105px;
	@media screen and (max-width : 767px) {
		padding-right: 20px;
		padding-left: 20px;
	}
}
.comission__list-item {
	position: relative;
	margin-bottom: 19px;
	font-size: 15px;
	line-height: 166.66%;
}

.comission__list-item-number {
	position: absolute;
	top: 50%;
	left: -48px;
	transform: translateY(-50%);
	font-size: 28px;
	line-height: 36px;
	font-weight: 900;
	color: $accent;
	-webkit-text-fill-color: transparent;
	-webkit-text-stroke-width: 1.5px;
	@media screen and (max-width : 767px) {
		position: relative;
    display: block;
    left: auto;
    transform: translateY(0);
	}
}
.comission__decor {
	position: absolute;
	bottom: 0;
	right: 70px;
	z-index: 1;
	@media screen and (max-width : 1849px) {
		transform: translateX(25%) scale(0.75);
	 }
	 @media screen and (max-width : 1599px) {
		transform: translateX(50%) scale(0.5);
	 }
	 @media screen and (max-width : 1299px) {
		display: none;
	 }
}


.sec-advantages {
	position: relative;
	padding-top: 140px;
	margin-top: -160px;
	padding-bottom: 145px;
	background-image: url(../img/sec-advantages__bg.png);
	background-size: cover;
	@media screen and (max-width : 767px) {
	 padding-bottom: 80px;
	}
	.advantages {
		margin-bottom: 65px;
		@media screen and (max-width : 767px) {
		 margin-bottom: 10px;
		}
	}
	.advantages__decor-bg {
		position: absolute;
		bottom: 57px;
		left: 0;
		@media screen and (max-width : 1365px) {
			transform: translateX(-5%) scale(0.75);
		 }
		 @media screen and (max-width : 991px) {
			display: none;
		 }
	}
	.advantages__decor-light {
		position: absolute;
		bottom: 57px;
		left: 0;
		@media screen and (max-width : 991px) {
		 display: none;
		}
	}
	.line-dash-1  {
		left: 0;
		top: 9%;
	}
	.line-dash-2  {
		left: 66.6666%;
		top: 44%;
	}
}
.sec-advantages__inner {
	position: relative;
	z-index: 1;
}
.sec-advantages__header {
	margin-bottom: 50px;
}
.advantages-info {
	z-index: 1;
	position: relative;
	margin-left: auto;
	max-width: 900px;
	.advantages-info__1 {
		padding: 15px 20px 80px 72px;
		background-color: $light;
		line-height: 189%;
		@media screen and (max-width : 767px) {
		 padding-left: 20px;
		}
	}
	.advantages-info__2 {
		margin: -65px 70px 0 83px;
		padding: 25px 85px 25px 100px;
		background-color: $accent;
		font-size: 36px;
		line-height: 139%;
		font-weight: 900;
		color: $light;
		text-align: center;
		@media screen and (max-width : 991px) {
		 padding-left: 30px;
		 padding-right: 30px;
		 font-size: 28px;
		@media screen and (max-width : 767px) {
			margin-left: 0;
			margin-right: 0;
			padding-left: 15px;
			padding-right: 15px;
			font-size: 20px;
		}
		}
	}
	.advantages-info__media {
		position: absolute;
		bottom: -115px;
    left: -130px;
		@media screen and (max-width : 991px) {
		 left: -190px;
		}
		@media screen and (max-width : 767px) {
		 display: none;
		}
	}
}

.sec-advantages-2 {
	position: relative;
	padding-top: 130px;
	[class^=col-] {
		display: flex;
	}
	.line-dash-1  {
		left: 0;
		top: 96%;
	}
	.line-dash-2  {
		left: 66.6666%;
		top: 12%;
	}
}
.sec-advantages-2__inner {
	position: relative;
	z-index: 1;
}
.sec-advantages-2__header {
	margin-bottom: 60px;
}
.advantages-2 {
	margin: 0 auto;
	max-width: 970px;
}
.advantages-2__item {
	margin-bottom: 30px;
	padding: 20px 70px 20px;
	background: $light;
	box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.1);
	width: 100%;
	@media screen and (max-width : 479px) {
	 padding: 20px;
	}
}
.advantages-2__item-title {
	line-height: 145%;
	font-weight: 900;
}
.advantages-2__item-media {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 80px;
}
.advantages-2__item-content {
	line-height: 145%;
}
.sec-cta {
	position: relative;
	margin-bottom: -200px;
	padding-top: 110px;
	@media screen and (max-width : 991px) {
	 margin-bottom: 60px;
	 padding-top: 60px;
	}

	.sec-cta__bg {
		z-index: -1;
		position: absolute;
		top: 0;
		right: 0;
		filter: blur(10px);
		background: url(../img/cta_bg.png);
		background-position: right center;
		background-size: cover;
		background-repeat: no-repeat;
		height: 100%;
		width: 100%;
	}
	.line-dash-1  {
		left: 66.6666%;
		top: 67%;
	}
	.line-dash-2  {
		right: -1px;
		top: 14%;
	}
}
.sec-cta__inner	{
	position: relative;
	z-index: 1;
}
.sec-cta__header {
	h2.title {
		margin-bottom: 50px;
		@media screen and (max-width : 1199px) {
		 margin-bottom: 10px;
		}
	}
	.descr {
		display: inline-block;
		margin-bottom: 70px;
		font-size: 36px;
		@media screen and (max-width : 1199px) {
			margin-bottom: 10px;
			font-size: 28px;
		 }
	}
}
.cta {
	display: flex;
	justify-content: space-between;
}
.cta__body {
	display: flex;
	@media screen and (max-width : 1199px) {
	 flex-direction: column;
	}
}
.cta__body-info {
	max-width: 470px;
	@media screen and (max-width : 1199px) {
	 margin-bottom: 30px;
	}
}
.cta__info {
	padding: 20px 22px;
	background: $accent;
	font-size: 24px;
	line-height: 142%;
}
.cta__phone {
	margin-left: 40px;
	padding: 30px 48px;
	background: $light;
	font-size: 36px;
	font-weight: 900;
	@media screen and (max-width : 550px) {
		font-size: 24px;
		margin-left: 0;
		padding: 20px;
	}
	a {
