}
.site-content {
	position: relative;
	z-index: 1;
}
.lines {
	position: absolute;
	top: 0;
	left: 50%;
	transform: translateX(-50%);
	display: flex;
	height: 100%;
	max-width: 1220px;
	width: 100%;
	overflow: hidden;
	@media screen and (max-width : 1279px) {
	 display: none;
	}
	.line {
		border-width: 0;
		border-style: solid;
		border-color:  rgba(235, 38, 41, 0.25);

		border-left-width: 1px;
		height: 100%;
		width: 33.3333%;
		&:last-child {
			border-right-width: 1px;
		}
	}
}
.lines-dashes {
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;
	left: 50%;
	transform: translateX(calc(-50% - 1px));
	max-width: 1220px;
	z-index: 0;
	@media screen and (max-width : 1279px) {
		display: none;
	 }
	.lines-dashes__wrap {
		position: relative;
		top: 0;
		left: 0;
		width: 100%;
    height: 100%;
	}
	.line-dash {
		position: absolute;
		width: 3px;
		height: 20px;
		background-color: $dark;
		&.accent {
			background-color: $accent;
		}
		&.light {
			background-color: $light;
		}
	}
}


